.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.button {
  display: inline-block;
  align-self: center;
  width: 80px;
  margin-right: 10px;
  padding: 4px 8px;
  background: none;
  border-radius: 5px;
  outline: none;
  border: 1px solid #55749e;
  background: #55749e;
  color: #fff;
}

.button:hover {
  background: #a868a2;
}
